<template>  

    <v-card flat tile>
        <v-parallax
            height="400"
            src="../assets/Images/Order/order_cover2.jpg"
        >
            <v-container fill-height >
                <v-row class="mx-auto">
                    <v-col>
                        <div class="text-left">
                        <div class="display-4 font-weight-light">
                            <p>Ready to order</p>
                        </div>

                        <div class="headline font-weight-light">
                            <p>Complete the form below and we will contact you!</p>
                        </div>
                        </div>
                    </v-col>
                </v-row>
            </v-container>
        </v-parallax>

        <v-form 
        ref="form"
        >
            <v-container>
                <div class="pt-5">

                    <p>*Required</p>

                    <p class = "headline"> Contact Information *</p>
                    <v-row dense>
                    <v-col    
                    cols = 8
                    md = 3
                    lg = 4
                    xl = 4
                    sm = 8           
                    >
                        <!--
                            xs screen -> cols = 3
                            sm screen -> cols = 7
                            md screen -> cols = 6
                            lg screen -> cols = 6
                        -->

                        <v-text-field
                            v-model="customerName"
                            clearable
                            prepend-inner-icon = "mdi-account"
                            color="blue-grey lighten-2"
                            label="Full Name"
                            counter="40"
                            :rules="customerNameRules"
                        ></v-text-field>
                    </v-col>

                    <v-col
                    cols = 8
                    md = 3
                    lg = 4
                    xl = 4
                    sm = 8        
                    >
                        <v-text-field
                            v-model="customerEmail"
                            clearable
                            prepend-inner-icon = "mdi-email"
                            color="blue-grey lighten-2"
                            label="Email Address"
                            counter="40"
                            :rules="customerEmailRules"
                        ></v-text-field>
                    </v-col>

                    <v-col
                    cols = 8
                    md = 3
                    lg = 4
                    xl = 4
                    sm = 8       
                    >
                        <v-text-field
                            v-model="customerNumber"     
                            clearable    
                            prepend-inner-icon = "mdi-phone"     
                            color="blue-grey lighten-2"
                            label="Phone Number"
                            v-mask="'+1 (###)-###-####'"
                            :rules = "customerNumberRules"
                        ></v-text-field>
                    </v-col>
                    </v-row>
                </div>

                <div class="pt-5">
                
                    <p class = "headline">Pick Up Date *</p>
                    
                    <v-row>
                        <v-col md = "4">
                            <v-menu
                                v-model="showCal"
                                :close-on-content-click="false"
                                transition="scale-transition"
                                offset-y
                                min-width="290px"
                            >
                                <template v-slot:activator="{ on }">
                                <v-text-field
                                    v-model="pickUpDate"
                                    :rules="pickUpDateRules"
                                    prepend-icon="mdi-calendar "
                                    readonly
                                    v-on="on"
                                ></v-text-field>

                                </template>
                                <v-date-picker 
                                    v-model="pickUpDate"
                                    show-current
                                    @input="showCal = false"
                                    :min="minOrderDate"
                                    :max="maxOrderDate"
                                    :allowed-dates= "allowedDates"
                                >
                                </v-date-picker>
                            </v-menu>
                        </v-col> 
                    </v-row>
                </div>

                <div class = "pt-5">
                    <v-row>
                        <v-col 
                        md = 4
                        lg = 4>

                        <p class = "headline">Pick Up Location *</p>

                        <v-radio-group 
                            v-model="pickUpLocation"
                            :rules="pickUpLocationRules"
                            >
                        <v-radio
                            v-for="location in pickUpLocations"
                            :key="location.locationId"
                            :label="location.locationName"
                            :value="location.locationName"
                        ></v-radio>
                        </v-radio-group>

                        </v-col>
                    </v-row>
                </div>

                <div class = "pt-5">
                    <p class = "headline">Cake Size and Flavor *</p>

                    <v-row>
                        <v-col md = "4">
                            <v-select
                            v-model="selectedCakeSize"
                            :items="cakeSizes"
                            label="Cake Size"
                            :rules="cakeSizeRules"
                            ></v-select>
                        </v-col>
                        <v-col md = "4">
                            <v-select
                            v-model="selectedCakeFlavor"
                            :items="cakeFlavors"
                            label="Cake Flavor"
                            :rules="cakeFlavorRules"
                            ></v-select>
                        </v-col>
                    </v-row>
                </div>

                <div class = "pt-5">
                    <p class = "headline">Cake Filling and Topping *</p>
                    <v-row>
                        <v-col md = "4">
                            <p>Filling (Can choose up to 2)</p>
                            
                            <v-checkbox  
                            v-model="selectedCakeFilling"
                            v-for="(filling, x) in cakeFillings"
                            :key=x
                            :label="filling" 
                            :value="filling"
                            :rules="cakeFillingRules"
                            :disabled="selectedCakeFilling.length >= 2 && selectedCakeFilling.indexOf(filling) === -1"
                            hide-details>
                            </v-checkbox>
                        </v-col>

                        <v-col md = "4">

                            <p>Toppings</p>
                            <v-checkbox  
                            v-model="selectedCakeTopping"
                            v-for="(topping, x) in cakeToppings"
                            :key=x
                            :label="topping" 
                            :value="topping"
                            :rules="caketoppingRules"
                            hide-details>
                            </v-checkbox>

                        </v-col>
                    </v-row>
                </div>

                <div class = "pt-5">
                    <p class = "headline">Cake Sayings and Special Request</p>
                    <v-row>
                        <v-col md = "4">
                            <p>Cake Saying (extra charge may applies)</p>
                            <v-textarea
                                v-model="cakeSaying"
                                counter = "500"
                                clearable
                                auto-grow
                                outlined
                                color="White"
                                label="Type your wording here"
                            ></v-textarea>
                        </v-col>

                        <v-col md = "4">
                            <p>Comments or special requests</p>
                            <v-textarea
                                v-model="cakeSpecialRequest"
                                counter = "500"
                                clearable
                                auto-grow
                                outlined
                                color="White"
                                label="Any special requests on your cake?"
                            ></v-textarea>
                        </v-col>

                        
                    </v-row>
                </div>
                
                <div class = "pt-5">      
                    <v-row>
                        <v-col md = "4"></v-col>
                        <v-col md = "4">
                            <v-row justify = "end">
                                <v-btn
                                    class="mr-4"
                                    @click="validate() "
                                    color="white"
                                >
                                Submit
                                </v-btn>

                                <v-btn
                                    :disabled="!valid"
                                    @click="reset()"
                                    color="white"
                                >
                                Clear form
                                </v-btn>
                            </v-row>
                        </v-col>
                    </v-row>
                </div>

            </v-container>  
        </v-form>

        <v-dialog
            v-model="loadingDialog"
            hide-overlay
            persistent
            width="300"
        >
            <v-card
            color="primary"
            dark
            >
                <div class = "pt-2"></div>
                <v-card-text>
                Processing your order ...
                    <v-progress-linear
                    indeterminate
                    color="white"
                    class="mb-0"
                    ></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
        
        <!--Confirmation window -->
        <div>
            <v-dialog
                v-model="confirmationDialog"
                width="1500"
                max-height="600"
                persistent
            >
                <v-card>
                    <v-card-title
                    class="headline grey lighten-2"
                    primary-title
                    >
                        <v-icon 
                        class = "mr-4" 
                        color ="green" 
                        x-large>mdi-check-circle-outline</v-icon>
                        Order Received!
                    </v-card-title>

                    <v-card-text class = "pt-5">
                        
                    <p class = "font-weight-medium display-1">Demis's Studio</p>

                    <p class = "font-weight-light title">A confirmation email has been sent to your email. We will soon contact you to finalize your order.</p> 
                   
                    <v-row>
                        <v-col>
                            <v-card flat tile>
                            <p class = "font-weight-light headline">Order # {{this.orderID}}</p>
                            </v-card>
                        </v-col>

                        <v-spacer></v-spacer>

                        <v-col>
                            <v-card flat tile>
                            <p class = "font-weight-light headline">{{this.formatDate(new Date())}}</p>
                            </v-card>
                        </v-col>
                    </v-row>

                    <v-divider></v-divider>

                        <v-row dense class = "pt-5">

                            <v-col cols = 5>

                                <div class = "font-weight-black headline">Contact Details</div>

                                <v-row dense>
                                    
                                    <v-col md = "6">
                                        
                                        <v-card-title class = "body-2">Name</v-card-title>
                                        <v-card-subtitle class = "title">{{this.customerName}}</v-card-subtitle>
                                        <!-- <p class = "font-weight-bold">{{this.customerName}}</p> -->
                                        
                                    </v-col>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Phone</v-card-title>
                                        <v-card-subtitle class = "title">{{this.customerNumber}}</v-card-subtitle>
                                    </v-col>
                                </v-row>

                                <v-row dense>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Email</v-card-title>
                                        <v-card-subtitle class = "title">{{this.customerEmail}}</v-card-subtitle>
                                    </v-col>        
                                </v-row>

                            </v-col>
                         
                            <v-col cols = 6>
                                <div class = "font-weight-black headline">Order Summary</div>

                                <v-row dense>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Pick up date</v-card-title>
                                        <v-card-subtitle class = "title">{{this.pickUpDate}}</v-card-subtitle>
                                    </v-col>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Pick up location </v-card-title>
                                        <v-card-subtitle class = "title">{{this.pickUpLocation}}</v-card-subtitle>
                                    </v-col>
                                </v-row>

                                <v-row dense>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Caka size:</v-card-title>
                                        <v-card-subtitle class = "title">{{this.selectedCakeSize}}</v-card-subtitle>
                                    </v-col>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Cake flavor: </v-card-title>
                                        <v-card-subtitle class = "title">{{this.selectedCakeFlavor}}</v-card-subtitle>
                                    </v-col>
                                </v-row>

                                <v-row dense>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Caka fillings: </v-card-title>
                                        <v-card-subtitle class = "title py-1" v-for="filling in selectedCakeFilling" :key="filling">{{filling}}</v-card-subtitle>
                                    </v-col>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Cake toppings:</v-card-title>
                                        <v-card-subtitle class = "title py-1" v-for="topping in selectedCakeTopping" :key="topping">{{topping}}</v-card-subtitle>
                                    </v-col>
                                </v-row>

                                <v-row dense>
                                    <v-col md = "6">
                                        <v-card-title class = "body-2">Saying:</v-card-title>
                                        <v-card-subtitle class = "title">{{this.cakeSaying}}</v-card-subtitle>
                                    </v-col>
                                    <v-col md = "6">
                                         <v-card-title class = "body-2">Special request:</v-card-title>
                                        <v-card-subtitle class = "title">{{this.cakeSpecialRequest}}</v-card-subtitle>
                                    </v-col>
                                </v-row>
                            </v-col>
                        </v-row>
                        <v-divider></v-divider>
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                        color="primary"
                        text
                        @click="confirmationDialog = false, orderReceived = false, reset(), resetVariable()"
                        >
                        Close
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </div>
    </v-card>

    
</template>

<script>

import axios from 'axios'

export default {
    name: 'Order',

    data: () => ({
        loadingDialog: false,
        orderReceived: false,
        confirmationDialog: false,
        valid: true,
        orderID: "",
        customerName: "",
        customerEmail: "",
        customerNumber: "",
        customerNameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length <= 10) || 'Name must be less than 10 characters',
        ],      
        customerEmailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
        ],    
        customerNumberRules: [
            v => (v && v.length == 17) || 'Phone number must be valid',
        ],
        pickUpDateRules : [
            v => !!v || 'Please select a pick up date'
        ],
        pickUpLocationRules : [
            v => !!v || 'Please select a pick up location'
        ],
        cakeSizeRules : [
            v => !!v || 'Please select a cake size',
        ],
        cakeFlavorRules : [
            v => !!v || 'Please select a cake flavor',
        ],
        pickUpDate: "",
        pickUpLocation: "",
        pickUpLocations :[
            {
                locationId : 1,
                locationName : "Asia Mart"
            },
            {
                locationId : 2,
                locationName : "Demie's Cake (Carmel)"
            }
        ],
        selectedCakeSize: "",
        cakeSizes : [
            "4 inch - $10",
            "6 inch - $20",
            "8 inch - $30",
            "10 inch - $50",
            "Stack (Special Order)"
        ],
        selectedCakeFlavor:"",
        cakeFlavors : [
            "Chocolate",
            "Macha",
            "Milk Tea",
            "Coffee",
            "Vanilla"
        ],
        selectedCakeFilling : [],
        cakeFillings : [
            "Whipped Cream Icing",
            "Baked Pudding - $5",
            "Red Bean - $5",
            "Taro - $5",
            "Diced Strawberry - $5",
            "Durian - $15"
        ],
        selectedCakeTopping : [],
        cakeToppings : [
            "Decortor's Choice",
            "Fresh Fruits",
            "Icing Flowers",
            "Bean Paste Flowers - extra charge"
        ],
        cakeSaying : "",
        cakeSpecialRequest : "",

        showCal: false,
        minOrderDate: new Date(Date.now()).toISOString().substr(0, 10),
        maxOrderDate: new Date(Date.now() + 7.884e+9).toISOString().substr(0, 10),
    }),

    watch: {
        orderReceived (val) {
            // We are setting a watch on the orderReceived variable, the orderReceived variable will be changed
            // upon the result return from the backend service

            if(val){
                // If the order is placed successfully

                // 1. Close the loading dialog after 2 sec
                setTimeout(() => (this.loadingDialog = false), 2000);
                
                // 2. Dispaly the confirmation dialog after 2 sec
                setTimeout(() => (this.confirmationDialog = true), 2000)
            }
        }
    },

    methods: {
        validate () {
            var validationResult = this.$refs.form.validate();
            //validationResult = true;
            if(validationResult){
                this.loadingDialog = true;
                this.submitOrder();
            }
        },

        reset () {
            this.$refs.form.reset()
        },

        allowedDates (val) {
            
            var currentDate = new Date();
            // Note: The val parameter is provided to the "allowedDates" function, it is the date with the format of YYYY-MM-DD.
            // https://stackoverflow.com/questions/50488703/vuetify-js-datepicker-provide-array-of-allowed-dates
            var dateCompare = new Date(val);

            var Difference_In_Time = dateCompare.getTime() - currentDate.getTime(); 
  
            // To calculate the no. of days between two dates 
            var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24); 
            // Orders must be placed 14 days ahead
            if(Difference_In_Days > 14) {
                return true;
            }
        },

        submitOrder(){
            
            if(this.cakeSaying === ""){
                this.cakeSaying = "(No cake saying)";
            }

            if(this.cakeSpecialRequest === "") {
                this.cakeSpecialRequest = "(No special requests)";
            }
            
            var orderDetail = {
                customerName : this.customerName,
                customerEmail : this.customerEmail,
                customerNumber : this.customerNumber,
                pickupDate : this.pickUpDate,
                pickupLocation : this.pickUpLocation,
                cakeSize : this.selectedCakeSize,
                cakeFlavor : this.selectedCakeFlavor,
                cakeFillings : this.selectedCakeFilling,
                cakeToppings : this.selectedCakeTopping,
                cakeSaying : this.cakeSaying,
                cakeSpecialRequest : this.cakeSpecialRequest,
            };
            
            /*
            var orderJSON = JSON.stringify(orderDetail);
            var testing = JSON.parse(orderJSON);
            console.log(this.customerNumber.length);
            console.log(testing);
            */
            
            //var url = "https://api.coindesk.com/v1/bpi/currentprice.json";
            var url = "http://lestari.homeip.net:18080/demiescakeservice-0.0.1-SNAPSHOT/submitOrder";

            axios
            .post(url, orderDetail)
            .then((response) => {

                //console.log(orderDetail);
                // Waiting for backend service to response

                // console.log(response);            
                // console.log(response.data);
                // console.log(response.status);
                // console.log(response.statusText);
                // console.log(response.headers);
                // console.log(response.config);

                // If response status is 200, which means an order is successfully placed:
                if (response.status === 200) {
                    
                    // 1. Retrieve the order number from response.data and update the order ID in the Data section
                    this.orderID = response.data;

                    // 2. Change the orderReceived variable to true, which triggers the watch functionality
                    // and makes the confirmation dialog to show 
                    this.orderReceived = true;

                    // The action items below will be executed in the confirmationDialog dialog
                    // 3. Reset variables
                    // 4. Follow-up actions include:
                    // 5 - Clear the order form
                    // 6 - reset dialog variable
                }
            })
            .catch((error) => {
                console.log(error)
            })
        },

        resetVariable() {
            this.customerName = "";
            this.customerEmail  = "";
            this.customerNumber = "";
            this.pickUpDate = "";
            this.pickUpLocation = "";
            this.selectedCakeSize = "";
            this.selectedCakeFlavor = "";
            this.selectedCakeFilling = [];
            this.selectedCakeTopping = [];
            this.cakeSaying = "";
            this.cakeSpecialRequest = "";
        },
        
        formatDate: function (date) {
            var monthNames = [
            "January", "February", "March",
            "April", "May", "June", "July",
            "August", "September", "October",
            "November", "December"
            ];

            var day = date.getDate();
            var monthIndex = date.getMonth();
            var year = date.getFullYear();

            return day + '-' + monthNames[monthIndex] + '-' + year;
        }
    },

    computed : {

        cakeFillingRules () {
            return [
                this.selectedCakeFilling.length > 0 || 'Please selcet cake fillings'
            ];
        },
        caketoppingRules () {
            return [
                this.selectedCakeTopping.length > 0 || 'Please selcet cake fillings'
            ];
        }
    } 

};
</script>

<style>

    .header-custom-title{
        color: black !important;
        font-family: "Great Vibes", cursive !important; 
        font-size: 40px;
    }
</style>
